.exercise {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  // background: rgba(62, 152, 199, 0.2);
  &__body {
    display: flex;
    flex-direction: column;
    height: 100%;
    border-radius: 5px;
    background: white;
  }

  &__image {
    display: flex;
    margin: auto;
    img {
      max-width: 100%;
    }
  }

  &__timer {
    display: flex;
    flex-basis: 50%;
    max-width: 175px;
    margin: auto;
  }

  .exercise-controls {
    display: flex;
    justify-content: space-around;
    font-size: 34px;
    width: 100%;
    color: #3e98c7;

    &__play {
      @include icon('play-2');
    }

    &__pause {
      @include icon('pause-2');
    }

    &__previous {
      @include icon('previous-2');
    }

    &__next {
      @include icon('next-2');
    }
  }
}
