.header {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  position: relative;
  background: rgba(62, 152, 199, 0.2);
  height: 60px;
  margin-bottom: 20px;

  &__icon {
    @include icon('cross');
    position: absolute;
    font-size: 18px;
    line-height: 22px;
    right: 15px;
    color: #404040;
  }

  &__text {
    text-align: center;
    font-size: 22px;
    font-weight: 600;
    line-height: 22px;
    color: darken(#3e98c7, 17%);
    margin: 0 25px;
  }
}