.exercise-list {
  display: flex;
  flex-direction: column;
  
  &__body {
    display: flex;
    flex-direction: column;
  }

  &__item {
    padding: 10px;
    margin: 0 20px 20px 20px;
    box-shadow: 1px 1px 3px 0px rgba(0,0,0,0.75);
    border-radius: 4px;

    &:hover {
      background: darken(white, 5%);
      cursor: pointer;
    }
  }

  &__start {
    width: 100%;
    text-align: center;
  }
}
